.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0 20px;
}
.header.main {
  height: 50px;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}
.header.pay {
  z-index: 2;
  height: 60px;
  line-height: 60px;
  text-align: center;
  background-color: #262439;
}
.header.pay > h1 {
  color: #fff;
  font-family: "Poppins", sans-serif;
}
.header.pay > a {
  position: absolute;
  left: 17px;
  color: #fff;
}
.header > h1 {
  font-size: 1.2rem;
}
.header > button {
  margin-top: 5px;
  border: 0;
  outline: none;
  background-color: transparent;
  font-size: 1.5rem;
}
.header > .btn {
  display: inline-block;
  margin-top: 3px;
  font-size: 1.4rem;
}
